"use client";

import React from "react";
import { merge } from "../../lib/utils";

export interface DashboardGridProps extends React.HTMLAttributes<HTMLDivElement> {
  columns?: 1 | 2 | 3 | 4 | 5 | 6;
  gap?: "sm" | "md" | "lg" | "xl";
  responsive?: boolean;
}

export const DashboardGrid = React.forwardRef<HTMLDivElement, DashboardGridProps>(
  (
    {
      columns = 4,
      gap = "md",
      responsive = true,
      className,
      children,
      ...props
    },
    ref
  ) => {
    const gridClasses = responsive
      ? columns === 1 ? "grid-cols-1"
      : columns === 2 ? "grid-cols-1 md:grid-cols-2"
      : columns === 3 ? "grid-cols-1 md:grid-cols-2 lg:grid-cols-3"
      : columns === 4 ? "grid-cols-1 md:grid-cols-2 lg:grid-cols-4"
      : columns === 5 ? "grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5"
      : "grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6"
      : columns === 1 ? "grid-cols-1"
      : columns === 2 ? "grid-cols-2"
      : columns === 3 ? "grid-cols-3"
      : columns === 4 ? "grid-cols-4"
      : columns === 5 ? "grid-cols-5"
      : "grid-cols-6";

    const gapClasses = gap === "sm" ? "gap-3"
      : gap === "md" ? "gap-6"
      : gap === "lg" ? "gap-8"
      : "gap-12";

    return (
      <div
        ref={ref}
        className={merge(
          "grid",
          gridClasses,
          gapClasses,
          className
        )}
        {...props}
      >
        {children}
      </div>
    );
  }
);

DashboardGrid.displayName = "DashboardGrid";

